html{
    overflow: hidden;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    height: 100%;
}

body{
    background: blanchedalmond;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

#root{
    padding: 5px;
    margin: 0;
    width: 100%;
    height: 100%;
}

#title{
    margin: 20px;
}

#container{
    display: flex;
}

#textAreaCont{
    left: 0;
    margin-left: 1vw;
    height: 85%;
    width: 45%;
    position: absolute;
}

#previewCont{
    border-radius: 4px;
    border-color: grey;
    overflow: scroll;
    margin-right: 1vw;
    position: absolute;
    border-style: solid;
    padding: 1vw;
    text-align: left;
    right: 0;
    height: 85%;
    width: 45%;
}
#previewCont::-webkit-scrollbar{
    display: none;
}

#editor{
    border-radius: 4px;
    font-size: 14px;
    font-family: monospace;
    background: blanchedalmond;
    padding: 1vw;
    width: 100%;
    height: 100%;
    border: grey solid;
}
#editor::-webkit-scrollbar{
    display: none;
}
#editor:focus{
    outline: none !important;
}

h1{
    margin: 0;
    padding: 0;
}

table ,th ,td{
    border-collapse: collapse;
    border-color: black;
    border-style: solid;
    border-width: 3px;
    padding: 5px;
}

img{
    max-width: 100%;
    height: auto;
}